import React, { useEffect, useRef, useCallback, memo, Suspense, useState } from "react";
import { connect } from "react-redux";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, FreeMode, Mousewheel } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/free-mode';
import 'swiper/css/mousewheel';
import './scss/ReelShop.scss';
import { isTagShop } from '../../../actions/api';
import { detectMobile } from "../../../utils";
import { nextData } from "../../../actions/moredataAction";
import Card from "./Elements/Card";

const ReelShop = memo((props) => {
  const {
    postData,
    completeDataObject,
    clickToShowPopUp,
    hasMoreData,
    parentID,
    keyRender,
    containerSize,
    onClickPopUpSlider,
    CardStyle,
    ThemeStyle,
    ProductSetting,
    ThemeInfo,
    appendData,
    preRender,
    loaderData,
    nextData,
    loadMoredata
  } = props;

  const sliderHeightRef = useRef(null);
  const inputRefs = useRef({});
  const [loadData, setloadData] = useState(false)

  const requestData = useCallback((swiper) => {  
    if (swiper.progress > 0.8 && hasMoreData && !loaderData.isShowMoreLoading) { 
      nextData(
        isTagShop ? ThemeInfo.webId : ThemeInfo.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle.totalPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }
  }, [hasMoreData, loaderData.isShowMoreLoading, nextData, ThemeInfo, ThemeStyle.totalPosts, appendData, preRender]);


  const updatepost = (e) => {  
     setloadData(true);
     setTimeout(() => loadMoredata() , (e*1000));
  };


  useEffect(() => {
    const classNameRole = document.querySelectorAll('.splide__slide');
    if (classNameRole) {
      classNameRole.forEach((element) => {
        element.removeAttribute("aria-hidden");
        element.removeAttribute("role");
      });
    }

    if (postData && postData.length === 1) {
      const arrowLeft = document.querySelector(".tb_rtp_arrow_left__");
      const arrowRight = document.querySelector(".tb_rtp_arrow_right__");
      if (arrowLeft && arrowRight) {
        arrowLeft.style.display = 'none';
        arrowRight.style.display = 'none';
      }
    }
  }, [postData]);

  const inputName = parentID;
  const numberOfColumns = ThemeStyle.column.desktop;
  const mobileColumn = ThemeStyle.column.mobile;
  const slidePost = ThemeStyle.slide.autoSlide;
  const slideDuration = ThemeStyle.slide.slideDuration;
  const trimContent = CardStyle.content.trimcontent === 1;
  const minCardSize = 240;
  const cardCount = Math.floor(containerSize.width / minCardSize);
  let responsiveSize = cardCount === 1 ? 2 : cardCount - 0.4;
  let newData = postData;

  if (responsiveSize > postData.length) responsiveSize = postData.length;
  const responsivePerView = numberOfColumns === 0 ? responsiveSize : parseInt(numberOfColumns) - 0.4;
  if (responsivePerView && Math.round(responsivePerView) <= postData.length) newData = Array(2).fill(postData).flat();


  return (
    <>
      {parentID && (
        <div className="tb_rtp_post_container" ref={sliderHeightRef}>
          <Swiper
            key={keyRender}
            ref={(el) => (inputRefs.current[inputName] = el)}
            style={{ width: '100%', height: '100%' }}
            onScroll={(swiper) => { requestData(swiper); if(!loadData){ updatepost(3)}}}
            onTouchMove={(swiper) => { requestData(swiper); if(!loadData){ updatepost(0) }}}
            onAutoplay={(swiper) => { requestData(swiper); if(!loadData){ updatepost(3) }}}
            
            autoHeight={true}
            speed={700}
            edgeSwipeDetection={true}
            slidesPerView={responsivePerView}
            touchRatio={2}
            cssMode={detectMobile()}
            passiveListeners={true}
            height={'100%'}
            freeMode={{ enabled: true, sticky: false, momentumBounceRatio: 2, momentumVelocityRatio: 3 }}
            autoplay={isTagShop ? { delay: slidePost === 1 ? slideDuration * 1000 : 99000, disableOnInteraction: false, pauseOnMouseEnter: true } : false}
            loop={!hasMoreData}
            slideFullyVisibleClass="tb-swiper-slide-fully-visible"
            breakpoints={{
              0: { slidesPerView: mobileColumn ? parseInt(mobileColumn) - 0.4 : responsiveSize },
              480: { slidesPerView: mobileColumn ? parseInt(mobileColumn) - 0.4 : responsiveSize },
              575: { slidesPerView: mobileColumn ? parseInt(mobileColumn) - 0.4 : responsiveSize },
              767: { slidesPerView: responsivePerView },
              1024: { slidesPerView: responsivePerView },
              1400: { slidesPerView: responsivePerView },
              1600: { slidesPerView: responsivePerView },
            }}
            mousewheel={{ forceToAxis: true, sensitivity: 2 }}
            watchSlidesProgress={true}
            modules={[Autoplay, Mousewheel, FreeMode]}
            className={`tb_rtp_post_slider ${trimContent ? 'tb_rtp_fixed_height' : ''}`}
          >
            {newData && newData.length > 0 && newData.map((item, index) => {
              const cardData = completeDataObject[item];
              return (isTagShop || [3, 5].includes(cardData.type)) ? (
                <SwiperSlide style={{ margin: 0, padding: 0 }} key={item.id} p_id={parentID} className={`tb_${cardData.id}`}>
                  {({ isVisible }) => (
                    <Card isVisible={isVisible} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} clickToShowPopUp={clickToShowPopUp} onClickPopUpSlider={onClickPopUpSlider} />
                  )}
                </SwiperSlide>
              ) : null;
            })}
          </Swiper>
        </div>
      )}
    </>
  );
});

const mapStateToProps = (state) => ({
  loaderData: state.loaderData,
});

const mapDispatchToProps = {
  nextData,
};

export default connect(mapStateToProps, mapDispatchToProps)(ReelShop);